<template>
  <div class="index1">
    <!---->
    <header class="aui-bar aui-bar-nav" id="aui-header">
      <a class="aui-btn aui-pull-left" tapmode="" onclick="history.go(-1)">
        <span class="aui-iconfont aui-icon-left"></span>
      </a>
      <div class="aui-title aui-font-size-12"></div>
    </header>
    <!---->

    <swiper :options="swiperOption">
      <template v-for="img in imgList">
        <swiper-slide>
          <img :src="'http://'+img.imgImgUrl">
          <!--<swiper-slide><img src="../../images/1.png"></swiper-slide>-->

        </swiper-slide>

      </template>
    </swiper>

    <!---->
    <div class="swiper-scrollbar" style="background: red;"></div>
    <!--<div class="swiper-button-next"></div>-->
    <!--<div class="swiper-button-prev"></div>-->
    <div class="swiper-pagination"></div>

    <!--  -->
    <div class="aui-content">
      <div class="aui-grid">
        <div class="aui-row" style="display: flex;">

          <div class="aui-col-xs-4 z-flex-column z-index-margin">
            <router-link to="/saoma/center">
              <img class="z-width-img" src="../../images/icon_shebeixufei.png" alt="">
              <div class="aui-grid-label aui-font-size-12"></div>
            </router-link>
          </div>

          <div class="aui-col-xs-4 z-flex-column z-index-margin">
            <router-link to="../center/tixian">
              <img class="z-width-img" src="../../images/icon_jifentixian.png" alt="">
              <div class="aui-grid-label aui-font-size-12"></div>
            </router-link>
          </div>

          <div class="aui-col-xs-4 z-flex-column z-index-margin">
            <router-link to="../center/jifentixian1">
              <img class="z-width-img" src="../../images/icon_dangqianjifen.png" alt="">
              <div class="aui-grid-label aui-font-size-12">
                
                <span class="z-colorju">{{summarySorce.userCurrentScore}}</span>
              </div>
            </router-link>
          </div>

        </div>
      </div>
    </div>

    <!---->
    <div class="clearfix bg-background">
    <div class="aui-card-list  z-margin-b0 z-shop2 z-padding-b-5">
    <router-link to="/index/daiyan">
    <div class="aui-card-list-content">
    <div class="aui-row aui-row-padded">
    <div class="aui-col-xs-12">
    <img src="../../images/banner03.png" style="height: 160px;">
    </div>
    </div>
    </div>

    <div class="aui-card-list-content-padded aui-padded-t-5 aui-padded-b-5 z-daiqu">
    
    </div>
    </router-link>


    </div>
    </div>

    <!---->
    <ul class="aui-list aui-list-in z-background-image z-margin-t1 bg-content">
      <li class="aui-list-item z-background-image bg-content">

        <div class="aui-list-item-inner">
          <div class="aui-list-item-title aui-font-size-12"></div>
          <div class="aui-list-item-right"><span class="z-colorhui aui-font-size-12"> >> </span></div>
        </div>
      </li>

    </ul>
    <!---->
    <div class="clearfix bg-content z-padding-b10 z-index-margin" style="margin-bottom: 100px !important;">
      <template v-for="items in lists">
        <div class="aui-card-list z-shop1 z-margin-b0"  style="line-height: 17px;">
          <!--<router-link to="/index/goods_detail" >-->
          <router-link :to="{path:'index/goods_detail',query:{goodsId:items.goodsId}}">
            <div class="aui-card-list-content">
              <div class="aui-row aui-row-padded">
                <div class="aui-col-xs-12">
                  <!-- http://-->
                  <img :src="'http://'+items.goodsImgList[0].goodsImgImg" style="height: 150px;">
                </div>
              </div>
            </div>
            <div class="aui-card-list-content-padded aui-padded-t-5 aui-padded-b-5 aui-ellipsis-1 aui-font-size-12">
              {{items.goodsTitle}}
            </div>
            <div class="aui-card-list-content-padded aui-padded-t-5 aui-padded-b-5 z-colorshenju"
                 style="text-align: left">
              {{items.goodsCurrnetScore}} 
            </div>
          </router-link>


        </div>
      </template>


    </div>


  </div>

</template>

<script>
  import axios from 'axios';

  import '../../css/swiper-3.4.2.min.css';
  import {swiper, swiperSlide} from 'vue-awesome-swiper'
  import 'swiper/dist/css/swiper.css'

  //vue
  export default {
    name: "index1",
    components: {
      swiper,
      swiperSlide
    },
    data() {
      return {

        userId: '1',
        pageNum: '1',
        pageSize: '6',
        lists: [],
        imgList:[],
        goodsImgGoodsId: '',
        summarySorce: {},


        //
        swiperOption: {//swiper3
          autoplay: 2000,
          speed: 1000,
          loop: true,

          autoplay: {
            delay: 1000, //ms
            disableOnInteraction: true, //swiperautoplay
            reverseDirection: false, //
            waitForTransition: true, //slide
          },
          //
          navigation: {
            nextEl: '.swiper-button-next', //cssHTML
            prevEl: '.swiper-button-prev', //cssHTML
            hideOnClick: true, //slide/
            disabledClass: 'my-button-disabled', //
            hiddenClass: 'my-button-hidden', //Class
          },
          //
          scrollbar: {
            el: '.swiper-scrollbar',
            hide: true, //false
            draggable: true, //true
            snapOnRelease: true, //falseslide
            dragSize: 10, //'auto': num(px)
          },

        }

      }
    },

    created: function () {
      //
      this.goodsList();
      this.cashSummary();


    },
    methods: {
      //
      goodsList: function () {
        //
        var data = new FormData();
        data.append('userId', this.userId);
        data.append('pageNum', this.pageNum);
        data.append('pageSize', this.pageSize);

        axios
          // .post('http://140.143.200.89:8080/dishwasher/firstPageSelectAll', data)
          .post('http://xiwanji.91xiaokong.com/firstPageSelectAll', data)
          .then(res => {
            var tempList = [];
            //
            // if (res.data.status== 200) {

            // console.log(JSON.stringify(res.data.goodsList.list));
            this.imgList = res.data.imgList;

            if (this.pageNum == 1) {

              this.lists = res.data.goodsList.list;

            } else {
              if (res.data.goodsList.list.length > 0) {
                for (var i = 0; i < res.data.goodsList.list.length; i++) {
                  tempList.push(res.data.goodsList.list[i])

                }
                this.lists = tempList;
              } else {
                alert('')
                return false;
              }
            }

            // }
            this.pageNum +=1;

            // console.log(JSON.stringify(this.pageNum));


          })
          .catch(error => {
            console.log(error)
          })

      },




      //
      cashSummary: function () {
        //
        var data = new FormData();
        data.append('userId', this.userId);


        axios
          // .post('http://140.143.200.89:8080/dishwasher/selectByCashSummary', data)
          .post('http://xiwanji.91xiaokong.com/selectByCashSummary', data)
          .then(response => {

            this.summarySorce = response.data
            console.log(JSON.stringify(this.summarySorce));


          })
      },


    },


  }
</script>

<style scoped>

  .aui-bar-tab .aui-active {
    color: #fd5a41;

  }

  .z-flex-column {
    display: flex !important;
    flex-direction: column;
    align-items: center;
  }

  .z-shop1 {
    width: 48%;
    float: left;
    margin-left: 3px;
    margin-right: 3px;
    border-radius: 5px;
    border: 1px solid #ebebeb;
    margin-top: 5px;
  }

  .z-shop2 {
    width: 98%;
    margin: 0px auto;

    margin-left: 3px;
    margin-right: 3px;
    border-radius: 5px;
    border: 1px solid #ebebeb;
  }

  .z-daiqu {
    text-align: center;
    padding-bottom: 50px;
    background: #fd5a41;
    color: #fff;
  }

  .z-index-margin {
    padding: 0px 0px 15px 0 !important;
    line-height: 0 !important;
    margin: 0 !important;
  }

  .z-index-margin {
    margin-top: -5px;
  }

  /**/
  .swiper-container {
    width: 100%;
    height: auto;
  }

  .swiper-button-next, .swiper-button-prev {
    position: absolute;
    top: 24%;
    width: 27px;
    height: 22px;
    /* margin-top: -22px; */
    z-index: 10;
    cursor: pointer;
    background-size: 8px 25px;
    background-position: center;
    background-repeat: no-repeat;
    color: #ddd !important;
  }


</style>




