<template>
    <div class="goods_detailI">
      <!---->
      <header class="aui-bar aui-bar-nav" id="aui-header">
        <a class="aui-btn aui-pull-left" tapmode="" onclick="history.go(-1)">
          <span class="aui-iconfont aui-icon-left"></span>
        </a>
        <div class="aui-title aui-font-size-12"></div>
      </header>
      <template v-if="lists.goods">
        <!---->
        <swiper :options="swiperOption">
          <!--<template v-for="img in lists">-->
          <template v-for="items in lists.goods.goodsImgList">
            <swiper-slide>
              <!--<img :src="'http://140.143.200.89:8080/dishwasher'+items.goodsImgImg" style="width:100%;height:200px;">-->
              <img :src="'http://'+items.goodsImgImg" style="width:100%;height:200px;">
              <!--<swiper-slide><img src="../../images/1.png"></swiper-slide>-->

            </swiper-slide>

          </template>
        </swiper>
        <!---->
        <div class="swiper-scrollbar" style="background: red;"></div>
        <div class="swiper-button-next"></div>
        <div class="swiper-button-prev"></div>
        <div class="swiper-pagination"></div>

        <!---->


        <div class="aui-list-item z-margin-t5">
          <div class="aui-media-list-item-inner">

            <div class="aui-list-item-inner">
              <div class="aui-list-item-text z-detail-tit aui-ellipsis-2">
                {{lists.goods.goodsTitle}}
              </div>
              <div class="aui-info aui-font-size-12 z-comment-detail">
                <div class="aui-info-item">
                  <span class="fontcolor aui-font-size-16 colorOrage">{{lists.goods.goodsCurrnetScore}}</span>
                  <span class="z-margin-l1 z-del z-colorhui">{{lists.goods.goodsOriginScore}}</span>
                </div>

              </div>



              <div class="aui-info aui-font-size-12 z-comment-detail">
                <div class="aui-info-item">
                  <span></span>
                  <span>{{lists.goods.goodsBusiness}}</span>
                </div>

              </div>

              <div class="aui-info aui-font-size-12 z-comment-detail z-margin-t5">
                <div class="aui-info-item"></div>
              </div>
              <div class="aui-card-list-content z-padding-b5">
                <div class="aui-row aui-row-padded">

                  <!-- -->
                  <div class="aui-col-xs-12" v-if="lists.goods.goodsDescribe">
                    <!--<img :src="'http://140.143.200.89:8080/dishwasher'+lists.goodsDescribe">-->
                    <img :src="'http://'+lists.goods.goodsDescribe">

                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>

        <!---->
        <div class="z-footer-fixed">
          <!--<router-link to="/index/confirm_order">-->
          <!--id -->
          <router-link :to="{path:'confirm_order',query:{goodsImgGoodsId:this.$route.query.goodsId}}">
            <div class="aui-btn aui-btn-info aui-btn-block aui-btn-sm aui-margin-t-15 btn-bg"></div>
          </router-link>
        </div>
      </template>

    </div>
</template>

<script>

    import '../../css/swiper-3.4.2.min.css';
    import { swiper, swiperSlide } from 'vue-awesome-swiper'
    import 'swiper/dist/css/swiper.css'
    import axios from 'axios'

    export default {
      name: "confirm_orderI",
      data(){
        return{
          goodsImgGoodsId:'',
          lists:{},

          //
          swiperOption: {//swiper3
            autoplay:2000,
            speed: 500,
            loop:true,

            autoplay: {
              delay: 500, //ms
              disableOnInteraction: true, //swiperautoplay
              reverseDirection: false, //
              waitForTransition: true, //slide
            },
            //
            navigation: {
              nextEl: '.swiper-button-next', //cssHTML
              prevEl: '.swiper-button-prev', //cssHTML
              hideOnClick: true, //slide/
              disabledClass: 'my-button-disabled', //
              hiddenClass: 'my-button-hidden', //Class
            },
            //
            scrollbar: {
              el: '.swiper-scrollbar',
              hide: true, //false
              draggable: true, //true
              snapOnRelease: true, //falseslide
              dragSize: 10, //'auto': num(px)
            },

          }

        }
      },
      created: function () {

        this.goodsDetail();
      },
      methods:{
        goodsDetail:function () {

          //goodId
          var newsID=this.$route.query.goodsId;
         console.log(JSON.stringify(newsID));
          axios
            .post('http://xiwanji.91xiaokong.com/selectToGoodsDetail?goodsImgGoodsId='+newsID)
            .then((response) => {
              // console.log(response);

              this.lists = response.data;
              // console.log(JSON.stringify(this.lists));

          }).catch(function(error) {
              console.log(error);
          });
        }

      },
      mounted(){

      },

    }
</script>

<style scoped>
  /**/
  .swiper-container {
    width: 90%;
    height:200px;
    margin: auto;
  }
  .swiper-button-next, .swiper-button-prev {
    position: absolute;
    top: 24%;
    width: 27px;
    height: 22px;

    z-index: 10;
    cursor: pointer;
    background-size: 8px 25px;
    background-position: center;
    background-repeat: no-repeat;
    color: #ddd !important;
  }

</style>



