<template>
    <div class="addshoukuangfashiI">
      <!---->
      <header class="aui-bar aui-bar-nav" id="aui-header">
        <a class="aui-btn aui-pull-left" tapmode="" onclick="history.go(-1)">
          <span class="aui-iconfont aui-icon-left"></span>
        </a>
        <div class="aui-title aui-font-size-12"></div>
      </header>

      <!---->

      <ul class="aui-list aui-form-list">
        <li class="aui-list-item">

          <div class="aui-list-item-inner">
            <!--<div class="aui-list-item-title aui-font-size-12"></div>-->
            <input type="text" class="aui-font-size-12" name="accountType" placeholder="" v-model="accountType">

          </div>
        </li>

        <li class="aui-list-item">
          <div class="aui-list-item-inner">
            <div class="aui-list-item-input">
              <input type="text" class="aui-font-size-12" name="accountAccountNumber" placeholder="" v-model="accountAccountNumber">
            </div>
          </div>
        </li>
        <li class="aui-list-item">
          <div class="aui-list-item-inner">

            <div class="aui-list-item-input">
              <input class="aui-font-size-12" type="text" name="accountNameReal" placeholder="" v-model="accountNameReal">
            </div>
          </div>
        </li>

        <li class="aui-list-item">
          <div class="aui-list-item-inner">

            <div class="aui-list-item-input">
              <input class="aui-font-size-12" type="number" name="accountMobile" placeholder="" v-model="accountMobile">
            </div>
          </div>
        </li>


      </ul>


      <!---->
      <div class="aui-btn aui-btn-info aui-btn-block aui-btn-sm z-margin-t100 btn-bg" @click="accountInsert($event)"></div>

    </div>
</template>

<script>
    import axios from 'axios'


    export default {
        name: "addshoukuangfashiI",
        data(){
            return{
              accountType:'',
              accountAccountNumber:'',
              accountMobile:'',
              accountNameReal:'',
              accountUserId:'1',

            }
        },
        created:function(){
        },
        methods:{
            //
                accountInsert:function (event) {
                  event.preventDefault();
                  var data = new FormData();
                  //
                  data.append('accountType', this.accountType);
                  //-
                  data.append('accountAccountNumber', this.accountAccountNumber);
                  //
                  data.append('accountMobile', this.accountMobile);
                  //
                  data.append('accountNameReal', this.accountNameReal);
                  //id
                  data.append('accountUserId', this.accountUserId);

                  var config ={
                    headers: {
                      'Content-Type': 'multipart/form-data'
                    }
                  }
                  var reg = 11 && /^((13|14|15|17|18)[0-9]{1}\d{8})$/;
                  //

                    if (this.accountType == '') {
                      alert('');
                      return;
                    } else if (this.accountAccountNumber == '') {
                      alert("");
                      return;
                    } else if (this.accountNameReal == '') {
                      alert("");
                      return;

                    } else if (this.accountMobile == '') {
                      alert("");
                      return;
                    } else if (!reg.test(this.accountMobile)) {
                      alert("");
                      return;
                    }

                  axios
                    .post('http://xiwanji.91xiaokong.com//accountInsert',data,config)
                    .then(function(res){
                      // console.log(JSON.stringify(res));
                      if(res.data.status == 200){
                        alert(JSON.stringify(res.data.message));

                      }else {
                        alert(JSON.stringify(res.data.message));
                      }
                    })

                }

        },
        mounted(){



        },

      components:{
        // mtactionsheet
      },






    }
</script>

<style scoped>

</style>



